:root{
  --bg-color:#040C18;
  --main-color:#042C54;
  --color-sup-text:#FF8A71;
  --color-text:#81afdd;
  --color-white: #fff;
  --gradient-bar: linear-gradient(103.22deg,#ae67fa -13.86%,#f49867 99.55%);
  --bg-gradient: radial-gradient(circle at 3% 25%,#002853 0,#040c18 25%);

}

@font-face {
  font-family: JFont;
  src: url(../src/fonts/JF\ Flat\ regular.ttf);
}
.font {
  font-family: JFont;
}
::-webkit-scrollbar {
  width: 7px;
  /* display: none; */
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--gradient-bar); 
  border-radius: 5px;
}
::-webkit-scrollbar-track {
  background: var(--bg-color);
}


body {
  background: var(--bg-gradient);
  scroll-behavior: smooth;
  overflow-x: hidden;
}
.nav-bg{
  background: var(--bg-gradient);
}
.nav-logo {
  color: var(--color-text);
  font-size: 25px;
  font-weight: 900;
  transition: all .3s;
}
.nav-logo:hover{
  color: var(--color-sup-text);
}
.search{
  border: none;
  border-radius: 50px;
}
.ov_text p{
  color: var(--color-text);
  font-size: 18px;
  font-weight: 600;
}
.link__text {
  text-decoration: none !important;
}
.card {
  box-shadow: 2px 1px 13px 1px rgba(0,0,0,0.3);
  position: relative;
  transition: all 0.5s;
  border: none;
  border-radius: 2rem;
}
.card:hover{
  transform: scale(1);
  margin-bottom: 50px;
  margin: 15px;
  border: 0;
}
.card__image {
  border-radius: 1.2rem;
}
.card:hover .overlay{
  background: var(--bg-color);
  opacity: 0.8;
}
.overlay {
  height: 100%;width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.5s;
  opacity: 0;
}
/* movie ditealis */

.img-movie{
  border-radius: 2rem;
  margin-bottom: 50px;
}
.card-text-details {
  font-size: 18px;
  color: var(--color-text);
  cursor: default;
}
.b-bottom {
  width: 250px;
  height: 2px;
  background: var(--gradient-bar);
  border-radius: 2px;
}
.card-text-story {
  color: var(--color-text);
}
.btn-outline-custom{
  border: 1px solid var(--main-color);
  color: var(--color-text);
  transition: all .5s;
  padding: 10px 15px;
}
.btn-outline-custom:hover {
  background: var(--main-color);
  color: var(--color-sup-text);
}
/* ========== ReactPaginate ============== */
.page-link {
  background: var(--main-color);
  border: var(--main-color);
  margin: 0 3px;
  color: var(--color-text);
  font-family: JFont;
  transition: all 0.5s;
  border-radius: 5px;
  border: none;
}
.page-link:hover {
  background: var(--color-sup-text);
  color: var(--color-white);
}
.prev {
  clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);

}
.next {
  clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
}
/* not-found */
.not-found {
  color: var(--color-sup-text);
  font-size: 40px;
}